	module.exports = {"data":{"markdownRemark":{"html":"<h2>Information</h2>\n<p>Join #hackgnv on the <a href=\"https://gnvconnect.com/\">GNV Connect</a> Slack Group for discussions.</p>\n<p><strong>Team Size:</strong> 1 - 4 people\n<strong>Team Makeup:</strong> Formed around common interest. Company, Meetup Group, Community Organization etc.<br>\n<strong>Cost:</strong> Free or $10 per person for t-shirts<br>\n<strong>Prize:</strong> Super Awesome Trophy</p>\n<h2>Introduction</h2>\n<p>Surrounding you, in Gainesville, lies a vast network of disparate communities, sizable local government and infrastructure, and a rich history of industry and culture. It’s up to you, the competitors of HackBattles: Gainesville to find new, exciting ways of connecting, informing, and otherwise improving the Gainesville experience.</p>\n<h2>Theme: Community Discovery</h2>\n<p>Teams are tasked with devising new ways for members of the community to connect with each other, learn more about Gainesville, or to become more involved and informed within ongoing community events. Quite simply, we’re looking for the best ways to explore, play, or cooperate within the Gainesville community!</p>","frontmatter":{"path":"/fl/gnv/discover-gainesville","title":"Discover Gainesville","location":"Gainesville, FL","address":"978 SW 2nd Ave.\nGainesville, FL 32601","date":"2017-08-26","time":"9:00am - 6:00pm EST","registrationUrl":"https://hackbattles.typeform.com/to/QwEfjF","venue":"Infinity Hall"}}},"pathContext":{}}

